.container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 2rem;
  margin: 0 3rem;
}

.textSection {
  grid-column: 2 / span 10;
  text-align: center;
}

.textSection h2 {
  margin-bottom: 1rem;
}
.textSection p {
  color: var(--clr-text-400);
  margin-bottom: 1rem;
}

.productCard {
  grid-column: span 3;
  overflow: hidden;
  border-radius: 10px;
}
.btn {
  grid-column: span 12;
  justify-self: center;
  align-self: center;
}

@media only screen and (max-width: 1200px) {
  .productCard {
    grid-column: span 4;
  }
}
@media only screen and (max-width: 800px) {
  .textSection {
    grid-column: 1 / span 12;
  }
  .productCard {
    grid-column: span 6;
  }
}

@media only screen and (max-width: 450px) {
  .container {
    margin: 0 2rem;
    gap: 0;
    row-gap: 2rem;
  }
  .productCard {
    grid-column: span 12;
  }
  .productCard img {
    height: 200px;
    object-fit: cover;
  }
}
