.grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    background-color: var(--clr-secondary);
    margin: 0 3rem;
    border-radius: 10px;
    overflow: hidden;
    padding: 4rem 0;
}

.statCard {
    grid-column: span 3;
    padding: 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 0.5rem;
    align-items: center;
    border-right: 1px solid rgba(59, 90, 253, 0.3);
}
.statCard:last-child {
    border: none;
}

.statCard h3 {
    color: var(--clr-accent);
}
.statCard p {
    color: var(--clr-text-400);
}

@media only screen and (max-width: 800px) {
    .statCard {
        grid-column: span 6;
        border-right: none;
    }
}

@media only screen and (max-width: 450px) {
    .statCard {
        grid-column: span 12;
        border-right: none;
        padding-bottom: 3rem;
    }
    .grid {
        margin: 0 2rem;
    }
}